  <template>
    <div v-loading="vitualLoading"
         element-loading-text="正在连接钱包" class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入虚拟币名称、简介、描述"
                      @keyup.native.enter="virtualQuery"
                      v-model="virtualcoinquery.chineseName">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" placeholder="查询所有"
                       v-model="virtualcoinquery.status">
                <el-option v-for="coinStatus in calendarTypeOptions" :key="coinStatus.key"
                           :label="coinStatus.display_name"
                           :value="coinStatus.key">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="virtualQuery" size="small">查询</el-button>
            <el-button type="primary" @click="handleDialogShow('add')" v-if="permission.add" size="small">新增</el-button>
        </div>
        <el-table
                ref="singleTable"
                :data="tableData"
                v-loading="listLoading"
                element-loading-text="给我一点时间"
                style="width: 100%">
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="250">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="modificationCoin(scope.row.baseSeq)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===1">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(2,scope.row.baseSeq,'启用')" v-if="permission.enable">启用
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-else-if="scope.row.status===2">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(1,scope.row.baseSeq,  '禁用')" v-if="permission.disable">禁用
                            </el-button>
                        </li>
                        <li style="float:left;width: 52px">
                            <el-button
                                    type="text" size="small"
                                    @click="measurementWallet(scope.row.baseSeq)" v-if="permission.testWallet">测试钱包
                            </el-button>
                        </li>
                        <li style="float:left;width: 52px">
                            <el-button
                                    type="text" size="small"
                                    @click="showFeesDialog(scope.row.baseSeq)" v-if="permission.edit">OTC费率
                            </el-button>
                        </li>
                        <li style="float:left ;width: 52px">
                            <el-button
                                    type="text" size="small"
                                    @click="getAddressOnId(scope.row.baseSeq)" v-if="permission.testWallet">生成地址
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="virtualcoinquery.page.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="virtualcoinquery.page.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="添加虚拟币类型" :visible.sync="confirm" @close="dialogClose" :close-on-click-modal="false">

            <el-tabs type="border-card">
                <!-- ###################################### 基础信息 ################################## -->
                <el-tab-pane label="基础信息">
                    <el-form :model="virtualCoinBase" :rules="rules" ref="virtualCoinBase">
                        <el-form-item :label-width="virtualWidth" label="*币种图标" prop="iconUrl">
                            <el-upload
                                    class="upload-demo"
                                    action="/upload/nofilter/file"
                                    list-type="picture"
                                    :on-success="handleSuccess"
                                    :on-remove="handleRemove"
                                    name="uploadFile"
                                    :before-upload="handleBeforeUpload"
                                    :autoUpload="true"
                                    :data="data"
                                    :file-list="fileList">
                                <el-button size="small" type="primary">上传图标</el-button>
                                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，512px*512px,且不超过2M</div>
                            </el-upload>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*币种名称" prop="chineseName">
                            <el-input class="input-short" v-model="virtualCoinBase.chineseName"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*币种简称" prop="shortName">
                            <el-input class="input-short" v-model="virtualCoinBase.shortName"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*英文名称" prop="englishName">
                            <el-input class="input-short" v-model="virtualCoinBase.englishName"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*符号" prop="symbol">
                            <el-input class="input-short" v-model="virtualCoinBase.symbol"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*ACCESS_KEY" prop="accessKey">
                            <el-input class="input-short" type="password" v-model="virtualCoinBase.accessKey"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*SECRET_KEY" prop="secretKey">
                            <el-input class="input-short" type="password" v-model="virtualCoinBase.secretKey"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="钱包密码" prop="walletPassword">
                            <el-input class="input-short" type="password" v-model="virtualCoinBase.walletPassword"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*IP地址" prop="addressIp">
                            <el-input class="input-short" v-model="virtualCoinBase.addressIp"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*端口号" prop="portNumber">
                            <el-input class="input-short" v-model="virtualCoinBase.portNumber"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否允许充值">
                            <el-switch
                                    v-model="virtualCoinBase.rechargeStatus"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否允许提现">
                            <el-switch
                                    v-model="virtualCoinBase.withdrawStatus"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否自动到帐">
                            <el-switch
                                    v-model="virtualCoinBase.autoRecharge"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否一代币">
                            <el-switch
                                    v-model="virtualCoinBase.firstStatus"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否ETH">
                            <el-switch
                                    v-model="virtualCoinBase.statusETH"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否USDT">
                            <el-switch
                                    v-model="virtualCoinBase.usdtStatus"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否ERC20">
                            <el-switch
                                    v-model="virtualCoinBase.erc20Status"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="汇总地址" prop="summaryAddress">
                            <el-input class="input-short" v-model="virtualCoinBase.summaryAddress"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="ERC20URL" prop="erc20Url">
                            <el-input class="input-short" v-model="virtualCoinBase.erc20Url"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="合约地址" prop="contractAddress">
                            <el-input class="input-short" v-model="virtualCoinBase.contractAddress"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="地址生成密码" prop="sonPassword">
                            <el-input class="input-short" v-model="virtualCoinBase.sonPassword"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="授权阀值" prop="threshold">
                            <el-input class="input-short" v-model="virtualCoinBase.threshold"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="授权金额" prop="authorizeAmount">
                            <el-input class="input-short" v-model="virtualCoinBase.authorizeAmount"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="合约小数位" prop="decimals">
                            <el-input-number class="input-short" v-model="virtualCoinBase.decimals" :min="0"
                                             :max="100"
                                             auto-complete="off"></el-input-number>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="地址生成路径" prop="mainPath">
                            <el-input class="input-short" v-model="virtualCoinBase.mainPath"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="主地址" prop="mainAddress">
                            <el-input class="input-short" v-model="virtualCoinBase.mainAddress"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="区块高度" prop="blockNum">
                            <el-input class="input-short" v-model="virtualCoinBase.blockNum"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="gasLimit" prop="gasLimit">
                            <el-input class="input-short" v-model="virtualCoinBase.gasLimit"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="nonce" prop="nonce">
                            <el-input class="input-short" v-model="virtualCoinBase.nonce"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否自动提币">
                            <el-switch
                                    v-model="virtualCoinBase.autoWithdraw"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否开启OTC交易">
                            <el-switch
                                    v-model="virtualCoinBase.legalFlag"
                                    on-text=""
                                    off-text=""
                                    :on-value="1"
                                    :off-value="0">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="充币确认次数">
                            <el-select v-model="virtualCoinBase.confirmNum" placeholder="请选择">
                                <el-option
                                        v-for="item in optionsNumber"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="OTC交易持有最少量" prop="leastNum">
                            <el-input class="input-short" v-model="virtualCoinBase.leastNum"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*OTC交易小数位" prop="priceNum">
                            <el-input-number class="input-short" v-model="virtualCoinBase.priceNum" :min="0"
                                             :max="10"
                                             auto-complete="off"></el-input-number>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每次最小提现数量" prop="minWithdrawAmt">
                            <el-input class="input-short" v-model="virtualCoinBase.minWithdrawAmt"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每次最大提现数量" prop="maxWithdrawAmt">
                            <el-input class="input-short" v-model="virtualCoinBase.maxWithdrawAmt"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每天最大提现数量" prop="maxWithdrawAmtDaily">
                            <el-input class="input-short" v-model="virtualCoinBase.maxWithdrawAmtDaily"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每天最大提现次数" prop="maxWithdrawNumDaily">
                            <el-input-number class="input-short" v-model="virtualCoinBase.maxWithdrawNumDaily" :min="1"
                                             :max="100"
                                             auto-complete="off"></el-input-number>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*虚拟币总量" prop="totalNum">
                            <el-input class="input-short" v-model="virtualCoinBase.totalNum"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <!--#####################################关于币种########################################-->
                <el-tab-pane label="关于币种">
                    <el-form :model="virtualCoinIntroduce" :rules="rules" ref="virtualCoinIntroduce">
                        <el-form-item :label-width="virtualWidth" label="是否显示关于币种">
                            <el-switch on-text="" off-text=""
                                       v-model="virtualCoinIntroduce.displayStatus" :on-value="2"
                                       :off-value="1"></el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="官网地址" prop="officialWebUrl">
                            <el-input class="input-short" v-model="virtualCoinIntroduce.officialWebUrl"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" prop="totalCirculation" label="发行总量">
                            <el-input class="input-short" v-model="virtualCoinIntroduce.totalCirculation"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" prop="totalStock" label="流通总量">
                            <el-input class="input-short" v-model="virtualCoinIntroduce.totalStock"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="众筹价格" prop="releasePrice">
                            <el-input class="input-short" v-model="virtualCoinIntroduce.releasePrice"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="发行日期" prop="releaseDate">
                            <el-date-picker
                                    v-model="virtualCoinIntroduce.releaseDate"
                                    type="date"
                                    @change="handleTimeChange"
                                    placeholder="选择日期">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="白皮书" prop="whitePaper">
                            <el-input class="input-short" v-model="virtualCoinIntroduce.whitePaper"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="区块查询" prop="blockExplorer">
                            <el-input class="input-short" v-model="virtualCoinIntroduce.blockExplorer"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="简介" prop="description">
                            <el-input class="input-short" type="textarea" v-model="virtualCoinIntroduce.description"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <!--###############################手续费用################################-->
                <el-tab-pane label="手续费用" class="m-serviceCharge">
                    <el-form :model="virtualCoinProcedure" :rules="rules" ref="virtualCoinProcedure">
                        <el-form-item :label-width="virtualWidth" label="等级[ 1 ]提现手续费：" prop="fetchOne">
                            <el-input class="input-fee" v-model="virtualCoinProcedure.fetchOne"
                                      auto-complete="off"></el-input>

                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="等级[ 2 ]提现手续费：" prop="fetchTwo">
                            <el-input class="input-fee" v-model="virtualCoinProcedure.fetchTwo"
                                      auto-complete="off"></el-input>

                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="等级[ 3 ]提现手续费：" prop="fetchThree">
                            <el-input class="input-fee" v-model="virtualCoinProcedure.fetchThree"
                                      auto-complete="off"></el-input>

                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="等级[ 4 ]提现手续费：" prop="fetchFour">
                            <el-input class="input-fee" v-model="virtualCoinProcedure.fetchFour"
                                      auto-complete="off"></el-input>

                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="等级[ 5 ]提现手续费：" prop="fetchFive">
                            <el-input class="input-fee" v-model="virtualCoinProcedure.fetchFive"
                                      auto-complete="off"></el-input>

                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="等级[ 6 ]提现手续费：" prop="fetchSix">
                            <el-input class="input-fee" v-model="virtualCoinProcedure.fetchSix"
                                      auto-complete="off"></el-input>

                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="等级[ 7 ]提现手续费：" prop="fetchSeven">
                            <el-input class="input-fee" v-model="virtualCoinProcedure.fetchSeven"
                                      auto-complete="off"></el-input>

                        </el-form-item>


                    </el-form>
                </el-tab-pane>
            </el-tabs>


            <div slot="footer" class="dialog-footer">
                <el-button @click="confirm = false">取 消</el-button>
                <el-button type="primary" @click="commitAction">确 认</el-button>
            </div>
        </el-dialog>

        <el-dialog
                :close-on-click-modal="false" title="生成地址" :visible.sync="centerDialogVisible" @close="address.size = '';address.password=''">
            <el-form :model="address">
                <el-form-item :label-width="virtualWidth" label="生成地址的数量">
                    <el-input-number class="input-300" v-model="address.size" :min="1"
                                     :max="10000"
                                     auto-complete="off"></el-input-number>
                </el-form-item>
                <el-form-item :label-width="virtualWidth" label="输入密码">
                    <el-input class="input-300" v-model="address.password" type="password"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                    <el-button @click="centerDialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="getAddress">确 定</el-button>
            </span>
        </el-dialog>

        <el-dialog title="修改OTC手续费" :visible.sync="feesDialogVisible" @close="feesDialogClose" :close-on-click-modal="false">
            <span style="color: #FF0000;position:absolute;top:22px;left:140px;">0.01 = 1%</span>
            <el-form :model="fees" :rules="ruleFees" ref="fees" class="m-serviceCharge">
                <el-form-item :label-width="itemWidth" label="等级[ 1 ]买入手续费：" prop="buyOne">
                    <el-input class="input-fee" v-model="fees.buyOne"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 1 ]卖出手续费：" prop="saleOne">
                    <el-input class="input-fee" v-model="fees.saleOne"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 2 ]买入手续费：" prop="buyTwo">
                    <el-input class="input-fee" v-model="fees.buyTwo"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 2 ]卖出手续费：" prop="saleTwo">
                    <el-input class="input-fee" v-model="fees.saleTwo"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 3 ]买入手续费：" prop="buyThree">
                    <el-input class="input-fee" v-model="fees.buyThree"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 3 ]卖出手续费：" prop="saleThree">
                    <el-input class="input-fee" v-model="fees.saleThree"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 4 ]买入手续费：" prop="buyFour">
                    <el-input class="input-fee" v-model="fees.buyFour"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 4 ]卖出手续费：" prop="saleFour">
                    <el-input class="input-fee" v-model="fees.saleFour"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 5 ]买入手续费：" prop="buyFive">
                    <el-input class="input-fee" v-model="fees.buyFive"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 5 ]卖出手续费：" prop="saleFive">
                    <el-input class="input-fee" v-model="fees.saleFive"
                              auto-complete="off"></el-input>
                </el-form-item>
