    import fetch from '@/utils/fetch';
    import {
        validateChinese,
        validatAlphabets,
        characterSize,
        validatePassword,
        validateIP,
        validateIpPort,
        validateSixDecimal,
        validateFourDecimal,
        validateTwoDecimal,
        validateTimeArea,
        validateDateTime,
        validateEightDecimal
