    const holidayBusinessScope = [{value: 1, label: '不限制'},
        {value: 2, label: '每周六-周日'},
        {value: 3, label: '中国法定节假日（含周末）'}];

    export default {
        data() {
            let validateUrl = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请上传图片'));
                }
                callback();
            };
            let validateChineseName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入名字'));
                }
                callback();
            };
            let validateShortName = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入简称'));
                if (characterSize(value) > 40) {
                    callback(new Error('简称最大长度40'));
                }
                callback();
            };
            let validateEnglishName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入英文名称'));
                }
                if (value.length > 40)
                    callback(new Error('请输入字母，最大长度40'));
                callback();
            };
            let validateSymbol = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入符号'));
                if (characterSize(value) > 4)
                    callback(new Error('符号最大长度4'));
                callback();
            };
            let validateAccessKey = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入accessKey'));
                if (!validatePassword(value) || value.length > 100)
                    callback(new Error('输入字母和数字，最大长度100'));
                callback();
            };
            let validateSecretKey = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入secretKey'));
                if (!validatePassword(value) || value.length > 100)
                    callback(new Error('输入字母和数字，最大长度100'));
                callback();
            };
            let validateWalletPassword = (rule, value, callback) => {
                if (value.length > 50)
                    callback(new Error('输入字母和数字，最大长度50'));
                callback();
            };
            let validateAddressIp = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入IP地址'));
                if (!validateIP(value))
                    callback(new Error('IP地址不合法'));
                callback();
            };
            let validatePortNumber = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入端口号'));
                if (!validateIpPort(value))
                    callback(new Error('端口号不合法'));
                callback();
            };
            let validateOpenPrice = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入开盘价'));
                if (!validateSixDecimal(value) || value * 1 > 10000000)
                    callback(new Error('开盘价最大为9999999，小数点后最多6位'));
                callback();
            };
            let validateRechargeAmount = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入允许充值最大数量'));
                if (!validateFourDecimal(value) || value * 1 > 1000000000)
                    callback(new Error('允许充值最大数量为999999999，小数点后最多4位'));
                callback();
            };
            let validateSummaryAddress = (rule, value, callback) => {
                if (characterSize(value) > 100)
                    callback(new Error('汇总地址最大长度100个字符'));
                callback();
            };

            let validateDurationTime = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请填写交易时间'));
                if (value === '24' || value === '0')
                    callback();
                if (!validateTimeArea(value))
                    callback(new Error('时间格式不正确'));
                callback();
            };
            let validateFees = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入手续费'));
                if (!validateFourDecimal(value) || value * 1 >= 10000)
                    callback(new Error('手续费最大数量为10000，小数点后最多4位'));
                callback();
            };
            let validateNormal = (rule, value, callback) => {
                if (characterSize(value) > 200)
                    callback(new Error('最多输入200个字符'));
                callback();
            };
            let validateDescription = (rule, value, callback) => {
                if (characterSize(value) > 500)
                    callback(new Error('最多输入500个字符'));
                callback();
            };
            let validateNumber = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入数量'));
                if (!validateEightDecimal(value) || value * 1 >= 999999999 || value <= 0)
                    callback(new Error('最大数量为999999999，小数点后最多8位'));
                callback();
            };
            return {
                itemWidth: '160px',
                centerDialogVisible: false,
                address: {
                    id: '',
                    size: '',
                    password: ''
                },
