        methods: {
            showDeleteDialog(id, str) {
                this.$confirm('确认生成地址？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {

                }).catch(() => {
                    this.$message.info('操作取消');
                });
            },
            handleDialogShow(action) {
                this.action = action;
                switch (action) {
                    case 'add':
                        this.confirm = true;
                        break;
                    case 'update':
                        this.modificationCoin();
                        break;
                }
            },
            commitAction() {
                switch (this.action) {
                    case 'add':
                        this.commitVirtual();
                        break;
                    case 'update':
                        this.modificationCoinBase();
                        break;
                }
            },
            dialogClose() {
                this.clearData();
                this.getList();
                this.fileList = [];
            },
            clearData() {
                this.resetData(this.virtualCoinBase);
                this.resetData(this.virtualCoinIntroduce);
                this.resetData(this.virtualCoinProcedure);
                this.$refs['virtualCoinBase'].resetFields();
                this.$refs['virtualCoinIntroduce'].resetFields();
                this.$refs['virtualCoinProcedure'].resetFields();
            },
            resetData(data) {
                let key;
                for (key in data) {
                    switch (key) {
                        case 'rechargeStatus':
                            data[key] = 2;
                            break;
                        case 'withdrawStatus':
                            data[key] = 2;
                            break;
                        case 'autoRecharge':
                            data[key] = 1;
                            break;
                        case 'autoWithdraw':
                            data[key] = 1;
                            break;
                        case 'firstStatus':
                            data[key] = 1;
                            break;
                        case 'statusETH':
                            data[key] = 1;
                            break;
                        case 'usdtStatus':
                            data[key] = 1;
                            break;
                        case 'confirmNum':
                            data[key] = 3;
                            break;
                        case 'displayStatus':
                            data[key] = 1;
                            break;
                        default:
                            data[key] = '';
                    }
                }
            },
            setData(newData, oldData) {
                let key;
                for (key in newData) {
                    switch (key) {
                        default:
                            newData[key] = oldData[key] === null ? '' : oldData[key];
                            break;
                    }

                }
            },
            checkFrom(formName) {
                this.$refs[formName].validate((valid) => {
                    return !!valid;
                });
            },
            //搜索功能
            getList() {
                this.listLoading = true;
                virtualcoin(this.virtualcoinquery).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    this.listLoading = false;
                })
            },
            //搜索按钮提交数据
            virtualQuery() {
                this.getList();
            },
            //翻页的按钮
            userPage(val) {
                this.virtualcoinquery.page.pageNum = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.virtualcoinquery.page.pageSize = val;
                this.getList();
            },
            //显示添加币种页面
            increasedCoin() {
                this.confirm = true;
            },
            //添加虚拟币
            commitVirtual() {
                let _this = this;
                this.$refs['virtualCoinBase'].validate((valid) => {
                    if (valid) {
                        this.$refs['virtualCoinIntroduce'].validate((valid) => {
                            if (valid) {
                                this.$refs['virtualCoinProcedure'].validate((valid) => {
                                    if (valid) {
                                        let data = {
                                            virtualCoinBase: _this.virtualCoinBase,
                                            virtualCoinIntroduce: _this.virtualCoinIntroduce,
                                            virtualCoinProcedure: _this.virtualCoinProcedure
                                        };
                                        increasedVirtualcoin(data).then(response => {
                                            if (response.result) {
                                                this.$message.success("添加成功!");
                                                this.confirm = false;
                                                this.getList();
                                            } else {
                                                this.$message.error(response.msg);
                                            }
                                        })
                                    } else {
                                        this.$message.error('手续费用输入错误');
                                    }
                                });
                            } else {
                                this.$message.error('关于币种输入错误');
                            }
                        });
                    } else {
                        this.$message.error('基础信息输入错误');
                    }
                });
            },

            //启用禁用虚拟币方法
            coinBase() {
                enableDisableCoin(this.strNum).then(response => {
                    if (response.result) {
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.getList();
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                });
            },
