            enableDisable(int, baseSeq, str) {
                /*let _this = this;
                if (_this.currentRow === null) {
                    this.$message('没有选择要' + str + '的虚拟币');
                } else if (int === _this.currentRow.status) {
                    this.$message('已经是' + str + '状态');
                } else {*/
                this.$confirm('要' + str + '此虚拟币,是否继续', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if (int === 1) {
                        this.strNum = '/' + baseSeq + '/forbid';
                        this.coinBase();
                    } else if (int === 2) {
                        this.strNum = '/' + baseSeq + '/enable';
                        this.coinBase();
                    }
                }).catch(() => {
                    this.$message.info('操作已取消');
                });

            },
            //修改虚拟币开始
            modificationCoin(int) {
                this.action = 'update';
                virtualCoinName(int).then(response => {
                    this.setData(this.virtualCoinBase, response.data.virtualCoinBase);
                    if (response.data.virtualCoinIntroduce !== null) {
                        this.setData(this.virtualCoinIntroduce, response.data.virtualCoinIntroduce);
                    }
                    this.setData(this.virtualCoinProcedure, response.data.virtualCoinProcedure);
                    this.fileList = [{name: '当前图片', url: response.data.virtualCoinBase.iconFullUrl}];
                });
                this.confirm = true;

            },
            modificationCoinBase() {
                let _this = this;
                console.log(this.virtualCoinProcedure);
                this.$refs['virtualCoinBase'].validate((valid) => {
                    if (valid) {
                        this.$refs['virtualCoinIntroduce'].validate((valid) => {
                            if (valid) {
                                this.$refs['virtualCoinProcedure'].validate((valid) => {
                                    if (valid) {
                                        let data = {
                                            virtualCoinBase: _this.virtualCoinBase,
                                            virtualCoinIntroduce: _this.virtualCoinIntroduce,
                                            virtualCoinProcedure: _this.virtualCoinProcedure
                                        };
                                        amendCoinBase(data, this.virtualCoinBase.baseSeq).then(response => {
                                            if (response.result) {
                                                this.$message.success("修改成功!");
                                                this.confirm = false;
                                                this.getList();
                                            } else {
                                                this.$message.error(response.msg);
                                            }
                                        })
                                    } else {
                                        this.$message.error('请填写手续费率');
                                    }
                                });
                            } else {
                                this.$message.error('关于币种输入错误');
                            }
                        });
                    } else {
                        this.$message.error('基础信息输入错误');
                    }
                });
            },
            //修改虚拟币结束
            //点击事件获取选中的单条信息的对象值
            /*handleCurrentChange(val) {
                this.currentRow = val;
            },*/
            getAddress() {
                let _this = this;
                this.vitualLoading = true;
                let newAddress = {
                    id: this.address.id,
                    size: this.address.size,
                    password: this.address.password
                };
                newAddress.password = encodePassword.encrypt(this.address.password);
                getNewAddress(newAddress).then(response => {
                    if (response.result) {
                        this.centerDialogVisible = false;
                    }
                    this.getMessage(response.msg, response.result);
                    this.vitualLoading = false;
                }).catch(function (response) {
                    _this.$message.error('请求失败');
                    _this.vitualLoading = false;
                })
            },
            getAddressOnId(id) {
                this.address.id = id;
                this.centerDialogVisible = true;
            },
            handleRemove(file, fileList) {
                this.virtualCoinBase.iconUrl = '';
            },
            handleSuccess(response, file, fileList) {
                this.fileList = [fileList[fileList.length - 1]];
                this.virtualCoinBase.iconUrl = response.data;
            },
            handleBeforeUpload(file) {
                let filename = file.name.toLowerCase();
                if (!filename.endsWith('.jpg') && !filename.endsWith('.jpeg') && !filename.endsWith('.png')) {
                    this.$message.error('格式不对，请重新选择');
                    return false;
                }
                if (file.size > 1024 * 1024 * 2) {
                    this.$message.error('图片太大，请重新选择');
                    return false;
                }
                return true;
            },
            handleDataChange(val) {
                this.virtualCoinBase.openTime = val;
            },

            measurementWallet(int) {
                let _this = this;
                this.vitualLoading = true;
                testCoinWallet(int).then(response => {
                    if (response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.vitualLoading = false;
                }).catch(function (response) {
                    _this.$message.error('连接失败');
                    _this.vitualLoading = false;
                });
            },
            handleTimeChange(time) {
                this.virtualCoinIntroduce.releaseDate = time;
            },
            feesDialogClose() {
                this.resetFees();
            },
            resetFees() {
                let key;
                for (key in this.fees) {
                    switch (key) {
                        default:
                            this.fees[key] = '0';
                    }
                }
            },
            showFeesDialog(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/getOtcTransactionFeesByCoinId/' + id ,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            if (response.data) {
                                _this.fees = response.data;
                            }
                            _this.fees.coinId = id;
                            _this.feesDialogVisible = true;
                        }
                    }).catch();
                });
            },
            modifyFees() {
                let _this = this;
                this.$refs['fees'].validate((valid) => {
                    if (valid) {
                        return new Promise((resolve, reject) => {
                            fetch({
                                url: 'admin/saveOrUpdateOtcTransactionFees',
                                method: 'put',
                                data: _this.fees
                            }).then(function (response) {
                                if (response.result) {
                                    _this.feesDialogVisible = false;
                                }
                                _this.getMessage(response.msg, response.result);
                            }).catch();
                        });
                    }
                });
            }
        },
        created() {
            this.getList();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }

    .input-fee {
        width: 90px;
    }

    .m-serviceCharge{
      .el-form-item{
        display: inline-block;
        width: 49%;
        margin-bottom: 10px;
      }
    }

</style>



